<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول فواتير العملاء</h4>
                      <div class="page-title-right">
                          <router-link :to="{name:'client_orders_create'}" tag="button" class="btn btn-success waves-effect waves-light">
                              إضافة فاتورة
                          </router-link>
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                    <div class="row m-4" dir="rtl">
                    <div class="col-md-4">
                      <v-select dir="rtl" label="name" class="vselect" style="width: 100%;"
                          :options="types" :reduce="val => val.id"></v-select> 
                    </div>
                    </div>
                      <div v-if="orders&&orders.data.length" class="card-body">
                          <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                              <thead>
                              <tr>
                                  <th>رقم الفاتورة</th>
                                  <th>العميل</th>
                                  <th>التليفون</th>
                                  <th>إجمالي الفاتورة</th>
                                  <th>مقدم</th>
                                  <th>شهور التقسيط</th>
                                  <th>تاريخ الفاتورة</th>
                                  <!--
                                  <th>تاريخ التسليم</th>
                                  -->
                                  <th>خيارات</th>
                              </tr>
                              </thead>
                              <tbody>
                              <tr v-for="(order,index) in orders.data">
                                  <td>#{{order.id}}{{order.created_at| moment("DDMMYY")}}</td>
                                  <td>{{order.name}}</td> 
                                  <td>{{order.phone1}}</td> 
                                  <td>{{moneyFormat(order.price+order.tax,0)}} L.E</td>
                                  <td>{{moneyFormat(order.deposit,0)}} L.E</td>
                                  <td v-if="order.status<2">
                                    <span :class="(order.payment_count==order.months)?'text-success':'text-danger'">
                                    {{order.payment_count}}</span>/{{order.months}}
                                  </td> 
                                  <td v-else>
                                    <span class="text-success">{{order.months}}</span>/{{order.months}}
                                  </td>
                                  <!--
                                  <td v-if="order.status==0" class="text-warning"><b>في الإنتظار</b></td>
                                  <td v-if="order.status==1" class="text-info"><b>مرحلة التقسيط</b></td>
                                  <td v-if="order.status==1" class="text-info"><b>مرحلة التقسيط</b></td>
                                  <td v-if="order.status==2" class="text-success"><b>تم السداد</b></td>
                                  <td>{{order.confirmed_at| moment("DD-MM-YYYY")}}</td>
                                  -->
                                  <td>{{order.created_at| moment("DD-MM-YYYY")}}</td>
                                  <td>
                                    <div class="btn-group">
                                        <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          المزيد <i class="mdi mdi-chevron-down"></i>
                                        </button>
                                        <div class="dropdown-menu" style="min-width: 120px;">
                                            <router-link :to="{ name: 'client_orders_show', params: { order_id: order.id } }" tag="a" class="dropdown-item">
                                                <i class="far fa-eye"></i> رؤية
                                            </router-link>
                                            <a @click="$refs.FormModal.destroyOrder(order.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-trash-alt"></i> حذف
                                            </a>
                                        </div>
                                    </div>
                                  </td>
                              </tr>
                              </tbody>
                          </table>
                          <pagination-nav v-if="orders.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                      </div>
                      <div v-else class="card-body">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                          <p v-else class="card-title-desc" style="text-align: center;">
                            لا يوجد بيانات مضافة حتى الآن
                          </p>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/client/add';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
        return {
            orders:null,loader:false,
            types:[
              {id:1,name:'تم الأستعلام عنها'},
              {id:2,name:'لم يستعلم عنها'},
              {id:3,name:'في الأرشيف'},
              {id:4,name:'تم تسليمها'},
            ]
        }
    },  
  created() { 
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.orders=null;
      this.loader=true;
      let data = {params: {page:num}};
      axios.get('/api/client_orders',data).then(({data}) => {
          this.orders=data.success.client_orders;
          if(!data.success.client_orders.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>